import { Injectable } from '@angular/core';
import { AlertController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class AuthErrorsService {

  constructor( private alertCtrl: AlertController ) { }

  async errorAlert( errorCode: string ) {

    switch ( errorCode ) {
      case 'auth/email-already-in-use':
        await this.presentAlert( 'Este correo electr칩nico ya est치 registrado.' );
      break;

      case 'auth/invalid-email':
        await this.presentAlert( 'El correo electr칩nico no es v치lido.' );
      break;
      
      case 'auth/wrong-password':
        await this.presentAlert( 'La contrase침a es inv치lida o el usuario no tiene una contrase침a.' );
      break;

      case 'auth/user-not-found':
        await this.presentAlert( 'No hay un usuario con este correo electr칩nico.' );
      break;

      case 'auth/email-already-in-use':
        await this.presentAlert( 'Este correo ya 칠sta registrado.' );
      break;
      default:
        break;
    }

  }

  async presentAlert( message: string ) {

    const alert = await this.alertCtrl.create({
      header: '',
      message,
      buttons: ['OK']
    });

    await alert.present();

  }

}
